<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div class="form__counter">
    <button
      type="button"
      aria-label="Убрать один товар"
      @click="decrementProductAmount"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="productAmount">

    <button
      type="button"
      aria-label="Добавить один товар"
      @click="incrementProductAmount"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>

export default {
  props: ['counter'],
  data() {
    return {
      productAmount: 'Во саду ли, в огороде',
    };
  },
  created() {
    if (this.counter) {
      this.productAmount = this.counter;
    }
  },
  methods: {
    incrementProductAmount() {
      this.productAmount += 1;
    },
    decrementProductAmount() {
      if (this.productAmount === 1) {
        return;
      }
      this.productAmount -= 1;
    },
  },
  watch: {
    productAmount(newValue, prevValue) {
      if (Number.isNaN(+this.productAmount) || this.productAmount <= 0) {
        this.productAmount = prevValue;
      } else {
        this.$emit('update:counter', +this.productAmount);
      }
    },
  },
};

</script>
