/* eslint-disable max-len */
import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import API_BASE_URL from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartProductLoading: false,
    cartProductLoadingError: false,
    errorMessage: '',
    orderInfo: null,
    orderInfoLoading: false,
    orderInfoLoadingError: false,
    delivery: 500,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((it) => it.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((prod) => ({
        productId: prod.product.id,
        amount: prod.quantity,
      }));
    },
  },
  getters: {
    deliveryGet(state) {
      return state.delivery;
    },
    orderInfoGet(state) {
      return state.orderInfo;
    },
    orderTotalAmountGet(state) {
      return state.orderInfo?.basket.items.reduce((acc, prod) => prod.quantity + acc, 0);
    },
    orderInfoLoadingGet(state) {
      return state.orderInfoLoading;
    },
    orderInfoLoadingErrorGet(state) {
      return state.orderInfoLoadingError;
    },
    errorMessageGet(state) {
      return state.errorMessage;
    },

    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const { product } = state.cartProductsData.find((p) => p.product.id === item.productId);
        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
    cartTotalAmount(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => item.amount + acc, 0);
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      this.state.orderInfoLoading = true;
      this.state.orderInfoLoadingError = false;
      clearTimeout(this.loadOrderInfoTimer);
      this.loadOrderInfoTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/orders/${orderId}`, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
          .then((response) => {
            context.commit('updateOrderInfo', response.data);
          })
          .catch((error) => {
            this.state.orderInfoLoadingError = true;
            this.state.errorMessage = `Ошибка при загрузке информации о заказе (статус: ${error.response.status.toString().slice(0, 3)}), повторите`;
          })
          .finally(() => {
            this.state.orderInfoLoading = false;
          });
      }, 1000);
    },
    loadCart(context) {
      this.state.cartProductLoading = true;
      this.state.cartProductLoadingError = false;
      return axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
        .catch((error) => {
          this.state.cartProductLoadingError = true;
          this.state.errorMessage = `Ошибка при загрузке корзины (статус: ${error.response.status.toString().slice(0, 3)}), повторите`;
        })
        .finally(() => { this.state.cartProductLoading = false; });
    },
    addProductToCart(context, { productId, amount }) {
      // eslint-disable-next-line no-promise-executor-return
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
        // eslint-disable-next-line arrow-body-style
        .then(() => {
          return axios
            .post(`${API_BASE_URL}/api/baskets/products`, {
              productId,
              quantity: amount,
            }, {
              params: {
                userAccessKey: context.state.userAccessKey,
              },
            })
            .then((response) => {
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts');
            });
        });
    },
    updateCartProductAmount(context, { productId, amount }) {
      // есть мутация с таким же названием, к ней и обратимся ниже
      context.commit('updateCartProductAmount', { productId, amount });
      if (amount < 1) { return; }
      // eslint-disable-next-line consistent-return
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteCartProduct(context, productId) {
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          data: { productId },
          params: { userAccessKey: context.state.userAccessKey },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
  },
});
