<!-- eslint-disable no-return-assign -->
<!-- eslint-disable import/extensions -->
<!-- eslint-disable linebreak-style -->
<!-- eslint-disable max-len -->
<!-- eslint-disable linebreak-style -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"/>

      <section class="catalog">

        <!-- <div v-if="productsLoading">Загрузка товаров...</div> -->
        <PreLoader v-if="productsLoading" />

        <!-- <div v-if="productsLoadingFailed">Произошла ошибка при загрузке</div> -->
        <ErrorLoading v-if="productsLoadingFailed" :message="errorMessage" :buttonAction="loadProducts" />
        <ProductList :products='products' />

        <BasePagination v-model='page' :count='countProducts' :per-page="productsPerPage" />

      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import API_BASE_URL from '@/config';
import PreLoader from '@/components/PreLoader.vue';
import ErrorLoading from '@/components/ErrorLoading.vue';

export default {
  components: {
    ProductList, BasePagination, ProductFilter, PreLoader, ErrorLoading,
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,

      errorMessage: '',

    };
  },
  computed: {
    products() {
      // const offset = (this.page - 1) * this.productsPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productsPerPage);
      return this.productsData
        // eslint-disable-next-line arrow-body-style
        ? this.productsData.items.map((product) => {
          return {
            ...product,
            image: product.image.file.url,
          };
        })
        : [];
    },
    countProducts() {
      // return this.filteredProducts.length;
      return this.productsData
        ? this.productsData.pagination.total
        : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      this.errorMessage = '';
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              colorId: this.filterColorId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
            },
          }, 1000)
        // eslint-disable-next-line no-return-assign
          .then((response) => this.productsData = response.data)
          // eslint-disable-next-line no-return-assign
          .catch((error) => {
            this.productsLoadingFailed = true;
            this.errorMessage = ` Ошибка при загрузке (статус: ${error.response.status.toString().slice(0, 3)}), попробуйте еще`;
          })
          // eslint-disable-next-line no-return-assign
          .then(() => this.productsLoading = false);
      }, 500);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};

</script>
